.box{
    height: 50px;
   text-align: center;
}

.thebutton{
    width: 250px;
    background-color: #f5ba13;
    color: white;
    height: 50px;
    border: none;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.3);
    margin: 0;
    top: 50%;
    border-radius: 5px;
}

.thebutton:hover{
    color: white;
    background-color: #f7c438;
}

.thebutton:active{
    transform: translateY(4px);
}

.trash{
    float: right;
    padding-right: 30px;
    font-size: large;
}

.text{
    float: left;
    text-transform: uppercase;
    font-size: large;
    padding-left: 30px;
}

